<template>
  <m-dialog
    title="选择用户"
    width="745px"
    :visible.sync="show"
    :close-on-click-modal="false"
    @confirm="handleConfirm"
  >
    <div class="mui-selectUsers">
      <div class="mui-selectUsers-tips">按历史批次快速选择</div>
      <el-row :gutter="8">
        <el-col :span="6">
          <el-select
            v-model="batchType"
            placeholder="请选择批次类型"
            @change="handleBatchSelect"
          >
            <el-option
              v-for="item in batchOptions"
              :key="item.batch_type"
              :label="item.batch_type"
              :value="item.batch_type"
            ></el-option>
          </el-select>
        </el-col>
        <el-col :span="18">
          <el-select
            class="mui-selectUsers-batch"
            v-model="batchText"
            filterable
            clearable
            placeholder="请选择批次"
            @change="handleBatchChange"
          >
            <el-option
              v-for="item in currentBatchList"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-col>
      </el-row>
      <div class="mui-selectUsers-search">
        <el-select
          style="width: 100%"
          v-model="userSelectedList"
          multiple
          filterable
          remote
          placeholder="请输入姓名或账号搜索用户"
          :remote-method="queryUserListBySearch"
          @change="handleChangeUserSelected"
          @focus="searchRemoteUserList = []"
          v-placeholder="
            userSelectedList.length ? '请输入姓名或账号搜索用户' : ''
          "
        >
          <el-option
            class="mui-selectUsers-options"
            v-for="item in searchRemoteUserList"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          >
            <m-tooltip
              class="mui-selectUsers-options_item"
              :content="
                item.name + `${item.account ? '（' + item.account + '）' : ''}`
              "
            >
              {{
                item.name + `${item.account ? "（" + item.account + "）" : ""}`
              }}
            </m-tooltip>
          </el-option>
        </el-select>
      </div>
      <div class="mui-selectUsers-box">
        <div class="mui-selectUsers-item">
          <div class="mui-selectUsers-item_header">组织架构</div>
          <div class="mui-selectUsers-item_body">
            <el-tree
              node-key="value"
              lazy
              highlight-current
              :props="orgTreeProps"
              :expand-on-click-node="false"
              :default-expanded-keys="orgTreeDefaultExpand"
              :load="handleLoadOrgTree"
              :render-content="renderOrgTreeContent"
              @current-change="orgSelectedChange"
            ></el-tree>
          </div>
        </div>
        <div class="mui-selectUsers-item">
          <div class="mui-selectUsers-item_header">用户列表</div>
          <div class="mui-selectUsers-item_body">
            <el-table
              ref="user-table"
              :data="userList"
              height="330px"
              :header-cell-style="{ background: '#fff' }"
              :cell-style="{ height: '50px' }"
              v-loading="userLoading"
              v-loadmore="queryUserByPage"
              @select="handleToggleSelection"
              @select-all="handleToggleSelection"
              empty-text="该组织下暂无相关用户"
            >
              <el-table-column type="selection" fixed="left" :width="50" />
              <el-table-column type="index" label="序号" :width="60" />
              <el-table-column
                prop="name"
                label="姓名"
                :width="100"
                show-overflow-tooltip
              />
              <el-table-column
                prop="account"
                label="账号"
                show-overflow-tooltip
              />
            </el-table>
          </div>
        </div>
      </div>
    </div>
  </m-dialog>
</template>

<script>
import { throttle } from "lodash-es";

export default {
  name: "MSelectUsers",
  props: {
    visible: {
      type: Boolean,
      default: false
    },
    // 用户列表区域 loading
    userLoading: {
      type: Boolean,
      default: false
    },
    // 当前批次信息
    currentBatchType: {
      type: String,
      default: null
    },
    // 批次数据
    batchOptions: {
      type: Array,
      default: () => []
    },
    // 组织架构数据
    orgTreeData: {
      type: Array,
      default: () => []
    },
    // 组织架构节点默认展开项
    orgTreeDefaultExpand: {
      type: Array,
      default: () => []
    },
    // 组织架构节点属性
    orgTreeProps: {
      type: Object,
      default: () => ({
        children: "childNode",
        label: "treeText",
        disabled: "disable",
        isLeaf: data => {
          // 用于判断是否为叶子节点，渲染加载更多
          return (data.childNum || 0) === 0;
        }
      })
    },
    // 用户列表
    userList: {
      type: Array,
      default: () => []
    }
  },
  directives: {
    loadmore: {
      bind(el, binding) {
        // 获取element-ui定义好的scroll盒子
        el.__scrollRl = el.querySelector(".el-table__body-wrapper");
        el.__callBackScroll__ = throttle(() => {
          const scrollTop =
            el.__scrollRl.scrollHeight - el.__scrollRl.scrollTop <=
            el.__scrollRl.clientHeight + 2;

          if (scrollTop) {
            binding.value();
          }
        }, 400);

        el.__scrollRl.addEventListener("scroll", el.__callBackScroll__, 300);
      },
      unbind(el) {
        el.__scrollRl.removeEventListener("scroll", el.__callBackScroll__);
        delete el.__callBackScroll__;
        delete el.__scrollRl;
      }
    },
    placeholder: {
      bind(el) {
        el.__input = el.querySelector(".el-select__input");
      },
      update(el, binding) {
        el.__input.setAttribute("placeholder", binding.value);
      }
    }
  },
  data() {
    return {
      show: false,
      batchType: void 0,
      selectedBatch: void 0,
      batchText: void 0,
      userSelectedList: [],
      userSelectedCacheData: [], // 缓存提交数据
      searchRemoteUserList: [] // 加载远程用户数据
    };
  },
  watch: {
    visible(nv) {
      // 清除选中批次信息
      this.batchText = undefined;
      this.show = nv;
    },
    show(nv) {
      this.$emit("update:visible", nv);
    },
    currentBatchType(nv) {
      this.batchType = nv;
    },
    // 更新表格用户列表数据后需要更新选中状态
    userList(nv) {
      if (!this.show || !nv.length) {
        return;
      }
      const selectedIds = [...this.userSelectedList];
      this.handleResetUserSelected(selectedIds);
    }
  },
  computed: {
    currentBatchList() {
      if (!this.batchOptions.length) {
        return [];
      }
      const currentBatch = this.batchOptions.find(
        item => item.batch_type === this.batchType
      );

      const defaultBatch = currentBatch?.batch_list || [];
      return defaultBatch;
    },
    userIdsMap() {
      const userIds = {};
      this.userList.forEach(row => {
        userIds[row.id] = row;
      });
      return userIds;
    }
  },
  methods: {
    renderOrgTreeContent(h, { node }) {
      return (
        <m-tooltip
          class="mui-selectUsers-tree"
          content={node.label}
          placement="right"
        >
          {node.label}
        </m-tooltip>
      );
    },
    // 重置表格用户选中状态
    handleResetUserSelected(selectedIds) {
      const userTable = this.$refs["user-table"];
      // 遍历当前用户数据，设置选中状态
      this.userList.forEach(row => {
        const selected = selectedIds.includes(row.id);
        userTable.toggleRowSelection(row, selected);
      });
    },
    /**
     * 用于暴露给外部调用，配合 ref 场景调用
     * @param remoteUsers 外部传入数据
     * @param onlyUpdateRemote 是否只更新远程下拉数据，用于展示选择下拉框
     */
    changeRemoteSearchUser(remoteUsers, onlyUpdateRemote) {
      if (onlyUpdateRemote) {
        this.searchRemoteUserList = remoteUsers;
        // 将数据缓存一份，防止搜索选中时出现数据找不到
        this.userSelectedCacheData = [
          ...this.userSelectedCacheData,
          ...remoteUsers
        ];
      } else {
        const selectedIds = remoteUsers.map(item => item.id);
        // 存在下拉后有选中的，需要更新表格列表的选中状态
        this.handleResetUserSelected(selectedIds);
        this.userSelectedList = selectedIds;
        this.userSelectedCacheData = remoteUsers;
        this.searchRemoteUserList = remoteUsers;
      }
    },
    /**
     * 切换批次
     */
    handleBatchSelect(value) {
      this.batchType = value;
      this.batchText = undefined;
    },
    /**
     * 切换当前批次选择
     */
    handleBatchChange(id) {
      this.$emit("batch-change", id);
    },
    /**
     * 点击选中当前组织
     */
    orgSelectedChange(data) {
      this.$emit("org-change", data);
    },
    /**
     * 获取远程搜索用户数据
     */
    queryUserListBySearch(keyword) {
      if (!keyword) {
        this.searchRemoteUserList = [];
        return;
      }
      const params = {
        // page: 1,
        // pageSize: 20,
        keyword
      };
      this.$emit("update-search-user", params);
    },
    /**
     * 更新子节点数据
     */
    getChildOrgTree(node, resolve) {
      this.$emit("update-org-tree", node.data, resolve);
    },
    /**
     * 加载树节点
     */
    handleLoadOrgTree(node, resolve) {
      if (node.level === 0) {
        return resolve(this.orgTreeData);
      }
      if (node.level === 1) {
        return resolve(this.orgTreeData[0].childNode);
      }
      this.getChildOrgTree(node, resolve);
    },
    handleToggleSelection(items) {
      // 更新远程下拉框的数据，用于渲染展示
      this.searchRemoteUserList = items;
      this.userList.forEach(row => {
        const index = this.userSelectedList.indexOf(row.id);
        /**
         * 场景一：列表存在选中，且选中项中不存在 => push
         * 场景二：列表不存在选中，且选中项存在于列表数据中 => splice
         */
        if (items.some(item => item.id === row.id) && index === -1) {
          this.userSelectedList.push(row.id);
          this.userSelectedCacheData.push(row);
        } else if (items.every(item => item.id !== row.id) && index > -1) {
          this.userSelectedList.splice(index, 1);
          this.userSelectedCacheData.splice(index, 1);
        }
      });
    },
    handleChangeUserSelected(data) {
      // 清空数据
      if (!data.length) {
        const userTable = this.$refs["user-table"];
        userTable.clearSelection();
        this.userSelectedCacheData = [];
        this.batchText = undefined;
        return;
      }

      // 过滤不存在的数据
      this.userSelectedCacheData = this.userSelectedCacheData.filter(item =>
        data.includes(item.id)
      );
      this.handleResetUserSelected(data);
    },
    // 滚动加载
    queryUserByPage() {
      this.$emit("update-user-list");
    },
    handleConfirm() {
      const data = [];
      // 校验数据，防止提交错误的数据
      this.userSelectedList.forEach(id => {
        const user = this.userSelectedCacheData.find(user => user.id === id);
        if (user) {
          data.push(user);
        }
      });

      this.$emit("confirm", data, () => {
        const userTable = this.$refs["user-table"];
        userTable.clearSelection();
        this.batchText = undefined;
        this.show = false;
      });
    }
  }
};
</script>
